@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&subset=cyrillic);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  color: #05386b;
  background-color: #5cdb95;
}

.task-list {
    width: 70%;
    display: flex;
    margin: 0 auto;
    flex-direction: column;
    justify-content: center;
}

@media (max-width: 40rem) {
    .task-list {
        width: 90%;
    }
}

.todo-list {
    padding: 0;
    margin-bottom: 0;
}

.todo-item__container {
    display: inline-block;
    width: 100%;
    background: #edf5e1;
    border: 5px solid #05386b;
    padding: 0.5rem;
}

.todo-item__container {
    display: inline-block;
    width: 100%;
    background: #edf5e1;
    border: 5px solid #05386b;
    padding: 0.5rem;
}

input[type='checkbox'] {
    display: none;
}

input[type='checkbox'] + label {
    display: inline-block;
    position: relative;
    padding-left: 2rem;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

input[type='checkbox'] + label:last-child {
    margin-bottom: 0;
}

input[type='checkbox'] + label:before {
    content: '';
    display: block;
    width: 1rem;
    top: 0.7rem;
    left: 0.25rem;
    height: 1rem;
    position: absolute;
    border: 1px solid #05386b;
    opacity: 0.7;
    -webkit-transition: all 0.12s, border-color 0.08s;
    transition: all 0.12s, border-color 0.08s;
}

input[type='checkbox']:checked + label:before {
    width: 0.5rem;
    top: 0.5rem;
    left: 0.25rem;
    border-radius: 0;
    opacity: 1;
    border-top-color: transparent;
    border-left-color: transparent;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

.todo-toDoItem {
    display: inline-block;
    cursor: pointer;
    font-size: 2rem;
    font-weight: bolder;
    margin: 0 auto;
    line-height: 1;
}

.todo-toDoItem:not(.active) {
    text-decoration: line-through;
}

.main-footer {
    display: inline-block;
    width: 100%;
    background: #edf5e1;
    border: 5px solid #05386b;
    border-top: 0;
    padding: 0.3rem;
}

.main-footer .button {
    margin-left: 0.5rem;
    box-shadow: 1px 1px 1px 1px #05386b;
}

.ui.button,
.ui.button:active,
.ui.button:hover,
.ui.button:focus {
  background-color: #edf5e1;
  color: #05386b;
}


.main-header { 
  box-shadow: 0 2px 1px 1px rgba(5, 56, 107, 0.5);
  margin: 1rem auto;
}

.main-header__title {
  display: inline-block;
}


.App {
  text-align: center;
}
